import React, { useState } from 'react';
import {
  Container,
  Typography,
  List,
  ListItem,
  ListItemText,
  IconButton,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Box,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Alert,
  CircularProgress,
  useTheme,
  useMediaQuery,
  Card,
  Divider
} from '@mui/material';
import { Delete, Add, Remove } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import axios from 'axios';
import BackButton from '../components/BackButton';

const validatePhoneNumber = (number) => {
  // Accept 10-digit phone numbers
  const phoneRegex = /^\d{10}$/;
  return phoneRegex.test(number.replace(/[^0-9]/g, ''));
};

const Cart = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  
  const navigate = useNavigate();
  const { user, login } = useAuth();  const [cartItems, setCartItems] = useState(() => {
    const savedCart = sessionStorage.getItem('cart');
    return savedCart ? JSON.parse(savedCart) : [];
  });
  const [openDialog, setOpenDialog] = useState(false);
  const [name, setName] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');

  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const [tableNumber, setTableNumber] = useState(() => {
    const storedTable = sessionStorage.getItem('tableNumber');
    return storedTable ? parseInt(storedTable) : 0;
  });

  const updateCart = (newCart) => {
    setCartItems(newCart);
    sessionStorage.setItem('cart', JSON.stringify(newCart));
  };

  const handleQuantityChange = (itemId, change) => {
    const newCart = cartItems.map(item => {
      if (item._id === itemId) {
        const newQuantity = Math.max(1, item.quantity + change);
        return { ...item, quantity: newQuantity };
      }
      return item;
    });
    updateCart(newCart);
  };

  const handleRemoveItem = (itemId) => {
    const newCart = cartItems.filter(item => item._id !== itemId);
    updateCart(newCart);
  };

  const calculateTotal = () => {
    return cartItems.reduce((total, item) => total + (item.price * item.quantity), 0);
  };

  const handlePlaceOrder = async () => {
    if (!tableNumber) {
      setError('Please select a table number');
      return;
    }
    if (!phoneNumber) {
      setOpenDialog(true);
      return;
    }
    if (!name && !user?.name) {
      setOpenDialog(true);
      return;
    }
    setError('');
    setLoading(true);

    try {
      // Prepare order data
      const orderData = {
        items: cartItems.map(item => ({
          foodItem: item._id,
          quantity: item.quantity,
          price: item.price
        })),
        totalAmount: calculateTotal(),
        tableNumber: parseInt(tableNumber),
        phoneNumber,
        customerName: name || user?.name
      };

      // Place the order - this will handle authentication/registration
      const response = await axios.post(
        'http://localhost:5000/api/orders',
        orderData
      );

      // Store authentication token and user info
      const { token, user: userInfo, order } = response.data;
      localStorage.setItem('token', token);
      await login({
        _id: userInfo._id,
        name: userInfo.name,
        phoneNumber: userInfo.phoneNumber,
        role: 'customer',
        orders: [order._id],
        token
      });

      sessionStorage.removeItem('cart');
      navigate('/orders');
    } catch (error) {
      console.error('Error placing order:', error);
      setError(error.response?.data?.message || 'Error placing order');
    } finally {
      setLoading(false);
    }
  };

  const handleDirectOrder = async () => {
    if (!name) {
      setError('Please enter your name');
      return;
    }
    
    if (!phoneNumber) {
      setError('Please enter your phone number');
      return;
    }

    if (!validatePhoneNumber(phoneNumber)) {
      setError('Please enter a valid 10-digit phone number');
      return;
    }

    await handlePlaceOrder();
  };

  if (cartItems.length === 0) {
    return (
      <Container 
        maxWidth="sm" 
        sx={{ 
          mt: { xs: 4, sm: 6 }, 
          textAlign: 'center', 
          position: 'relative' 
        }}
      >
        <BackButton />
        <Typography 
          variant="h5"
          sx={{ mb: 3, fontSize: { xs: '1.5rem', sm: '1.75rem' } }}
        >
          Your cart is empty
        </Typography>
        <Button
          variant="contained"
          color="primary"
          size={isMobile ? "medium" : "large"}
          onClick={() => navigate('/')}
          sx={{ px: { xs: 3, sm: 4 } }}
        >
          Browse Menu
        </Button>
      </Container>
    );
  }

  return (
    <Container 
      maxWidth="md" 
      sx={{ 
        mt: { xs: 2, sm: 4 },
        px: { xs: 2, sm: 3 },
        position: 'relative'
      }}
    >
      <BackButton />
      <Typography 
        variant="h4" 
        sx={{
          mb: { xs: 2, sm: 3 },
          fontSize: { xs: '1.5rem', sm: '2rem', md: '2.125rem' }
        }}
      >
        Place Order
      </Typography>

      <Card 
        elevation={2} 
        sx={{ 
          mb: { xs: 2, sm: 4 },
          overflow: 'hidden'
        }}
      >
        <List disablePadding>
          {cartItems.map((item, index) => (
            <React.Fragment key={item._id}>
              {index > 0 && <Divider />}
              <ListItem 
                sx={{
                  py: { xs: 2, sm: 2.5 },
                  px: { xs: 2, sm: 3 },
                  flexDirection: { xs: 'column', sm: 'row' },
                  alignItems: { xs: 'stretch', sm: 'center' },
                }}
              >
                <ListItemText
                  primary={
                    <Typography 
                      variant="h6"
                      sx={{
                        fontSize: { xs: '1rem', sm: '1.125rem' },
                        mb: { xs: 1, sm: 0 }
                      }}
                    >
                      {item.name}
                    </Typography>
                  }
                  secondary={
                    <Typography 
                      variant="body2" 
                      color="text.secondary"
                      sx={{
                        fontSize: { xs: '0.875rem', sm: '1rem' }
                      }}
                    >
                      ${item.price.toFixed(2)}  {item.quantity}
                    </Typography>
                  }
                />
                <Box
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: { xs: 'space-between', sm: 'flex-end' },
                    mt: { xs: 2, sm: 0 },
                    gap: { xs: 1, sm: 2 }
                  }}
                >
                  <Box sx={{ display: 'flex', alignItems: 'center' }}>
                    <IconButton 
                      onClick={() => handleQuantityChange(item._id, -1)}
                      size="small"
                    >
                      <Remove fontSize="small" />
                    </IconButton>
                    <Typography 
                      sx={{ 
                        mx: { xs: 1, sm: 2 },
                        minWidth: '24px',
                        textAlign: 'center'
                      }}
                    >
                      {item.quantity}
                    </Typography>
                    <IconButton 
                      onClick={() => handleQuantityChange(item._id, 1)}
                      size="small"
                    >
                      <Add fontSize="small" />
                    </IconButton>
                  </Box>
                  
                  <Typography 
                    variant="body1" 
                    sx={{ 
                      fontWeight: 'medium',
                      minWidth: '80px',
                      textAlign: 'right',
                      display: { xs: 'none', sm: 'block' }
                    }}
                  >
                    ${(item.quantity * item.price).toFixed(2)}
                  </Typography>

                  <IconButton
                    onClick={() => handleRemoveItem(item._id)}
                    color="error"
                    size="small"
                  >
                    <Delete fontSize="small" />
                  </IconButton>
                </Box>
              </ListItem>
            </React.Fragment>
          ))}
        </List>
      </Card>

      <Box sx={{ mb: 4 }}>
        {error && (
          <Alert 
            severity="error" 
            sx={{ mb: 2 }}
            onClose={() => setError('')}
          >
            {error}
          </Alert>
        )}
        
        <FormControl 
          fullWidth 
          sx={{ mb: 3 }}
        >
          <InputLabel id="table-number-label">Table Number</InputLabel>
          <Select
            labelId="table-number-label"
            value={tableNumber}
            label="Table Number"
            onChange={(e) => setTableNumber(e.target.value)}
            sx={{
              '.MuiSelect-select': {
                py: { xs: 1.5, sm: 2 }
              }
            }}
          >
            {[...Array(20)].map((_, index) => (
              <MenuItem key={index + 1} value={index + 1}>
                Table {index + 1}
              </MenuItem>
            ))}
          </Select>
        </FormControl>

        <Box 
          sx={{ 
            display: 'flex',
            flexDirection: { xs: 'column', sm: 'row' },
            justifyContent: 'space-between',
            alignItems: { xs: 'stretch', sm: 'center' },
            gap: 2
          }}
        >
          <Typography 
            variant="h5" 
            sx={{ 
              fontSize: { xs: '1.25rem', sm: '1.5rem' },
              fontWeight: 'medium'
            }}
          >
            Total: ${calculateTotal().toFixed(2)}
          </Typography>
          <Button
            variant="contained"
            color="primary"
            size={isMobile ? "large" : "medium"}
            onClick={handlePlaceOrder}
            disabled={loading}
            sx={{
              minWidth: { xs: '100%', sm: '200px' },
              py: { xs: 1.5, sm: 1 }
            }}
          >
            {loading ? (
              <CircularProgress size={24} color="inherit" />
            ) : (
              'Place Order'
            )}
          </Button>
        </Box>
      </Box>

      <Dialog 
        open={openDialog} 
        onClose={() => !loading && setOpenDialog(false)}
        fullWidth
        maxWidth="xs"
      >
        <DialogTitle 
          sx={{ 
            pb: 1,
            fontSize: { xs: '1.25rem', sm: '1.5rem' }
          }}
        >
          Complete Your Order
        </DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label="Name"
            fullWidth
            value={name}
            onChange={(e) => setName(e.target.value)}
            disabled={loading}
            sx={{ mb: 2 }}
          />
          <TextField
            margin="dense"
            label="Phone Number"
            fullWidth
            value={phoneNumber}
            onChange={(e) => setPhoneNumber(e.target.value)}
            disabled={loading}
          />
        </DialogContent>
        <DialogActions sx={{ px: 3, py: 2 }}>
          <Button 
            onClick={() => setOpenDialog(false)} 
            disabled={loading}
          >
            Cancel
          </Button>
          <Button 
            onClick={handleDirectOrder}
            variant="contained" 
            color="primary"
            disabled={loading}
          >
            {loading ? (
              <CircularProgress size={24} color="inherit" />
            ) : (
              'Place Order'
            )}
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default Cart;