import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const OrderTimeline = ({ comments }) => {
  if (!comments || comments.length === 0) return null;

  const getStatusColor = (status) => {
    const statusColors = {
      pending: 'bg-amber-500 text-white',
      preparing: 'bg-blue-500 text-white',
      ready: 'bg-emerald-500 text-white',
      delivered: 'bg-indigo-500 text-white',
      cancelled: 'bg-red-500 text-white',
      completed: 'bg-green-600 text-white'
    };
    return statusColors[status] || 'bg-gray-500 text-white';
  };

  return (
    <div className="relative pl-8">
      {/* Vertical line */}
      <div className="absolute left-4 top-0 bottom-0 w-0.5 bg-gray-200"></div>
      
      {comments.map((comment, index) => (
        <div key={index} className="relative mb-6 last:mb-0">
          {/* Dot */}
          <div className={`absolute left-0 top-0 w-6 h-6 rounded-full flex items-center justify-center ${getStatusColor(comment.status)} transform -translate-x-1/2`}>
            <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
              <circle cx="10" cy="10" r="10" />
            </svg>
          </div>

          {/* Content */}
          <div className="mb-2">
            <div className="flex items-center gap-2 mb-1">
              <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(comment.status)}`}>
                {comment.status.charAt(0).toUpperCase() + comment.status.slice(1)}
              </span>
              <span className="ml-auto text-xs text-gray-500">
                {new Date(comment.timestamp).toLocaleString()}
              </span>
            </div>
            <p className="text-sm text-gray-600 pl-2">
              {comment.text}
            </p>
          </div>
        </div>
      ))}
    </div>
  );
};

const Orders = () => {
  const [orders, setOrders] = useState([]);
  const [paymentOpen, setPaymentOpen] = useState(false);
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [paymentMethod, setPaymentMethod] = useState('cash');
  const [paymentError, setPaymentError] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [userProfile, setUserProfile] = useState(null);
  const [filterStatus, setFilterStatus] = useState('all');
  const [processingPayment, setProcessingPayment] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchUserData = async () => {
      setLoading(true);
      setError(null);
      try {
        const token = localStorage.getItem('token');
        if (!token) {
          setError('Please log in to view your orders');
          navigate('/login');
          return;
        }

        const headers = {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        };

        try {
          const profileResponse = await axios.get('http://localhost:5000/api/auth/profile', { headers });
          setUserProfile(profileResponse.data);

          const ordersResponse = await axios.get('http://localhost:5000/api/orders/my-orders', { headers });
          const sortedOrders = ordersResponse.data.sort((a, b) => 
            new Date(b.createdAt) - new Date(a.createdAt)
          );
          setOrders(sortedOrders);
        } catch (error) {
          if (error.response?.status === 404) {
            setError('No profile found. Please complete your profile first.');
          } else if (error.response?.status === 401) {
            localStorage.removeItem('token');
            setError('Your session has expired. Please log in again.');
            navigate('/login');
          } else {
            setError('Failed to load orders or profile. Please try again later.');
            console.error('API Error:', error.response?.data || error.message);
          }
          throw error;
        }
      } catch (error) {
        console.error('Error in fetchUserData:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchUserData();
  }, [navigate]);

  const filteredOrders = orders.filter(order => 
    filterStatus === 'all' ? true : order.status === filterStatus
  );

  const handlePaymentClick = (order) => {
    setSelectedOrder(order);
    setPaymentMethod('cash');
    setPaymentOpen(true);
    setPaymentError(null);
  };

  const handlePaymentClose = () => {
    setPaymentOpen(false);
    setSelectedOrder(null);
    setPaymentError(null);
  };

  const handlePaymentSubmit = async () => {
    if (!paymentMethod) {
      setPaymentError('Please select a payment method');
      return;
    }

    setProcessingPayment(true);
    setPaymentError(null);

    try {
      const token = localStorage.getItem('token');
      if (!token) {
        setPaymentError('You need to be logged in to make a payment');
        setTimeout(() => {
          navigate('/login');
        }, 2000);
        return;
      }

      // Process payment
      const headers = {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      };

      const { data: updatedOrder } = await axios.patch(
        `http://localhost:5000/api/orders/${selectedOrder._id}/payment`,
        {
          paymentMethod,
          paymentStatus: 'completed'
        },
        { headers }
      );

      // Update orders list with new order status
      setOrders(orders.map(order => 
        order._id === selectedOrder._id ? { ...order, ...updatedOrder } : order
      ));

      // Refresh user profile to get latest orders
      try {
        const profileResponse = await axios.get(
          'http://localhost:5000/api/auth/profile',
          { headers }
        );
        setUserProfile(profileResponse.data);
      } catch (profileError) {
        console.error('Error refreshing profile:', profileError);
        // Don't block the payment completion if profile refresh fails
      }

      handlePaymentClose();
    } catch (error) {
      console.error('Error processing payment:', error);
      if (error.response?.status === 401) {
        setPaymentError('Your session has expired. Please log in again.');
        localStorage.removeItem('token');
        setTimeout(() => {
          navigate('/login');
        }, 2000);
      } else {
        setPaymentError(
          error.response?.data?.message || 
          'Failed to process payment. Please try again.'
        );
      }
    } finally {
      setProcessingPayment(false);
    }
  };

  const getStatusColor = (status) => {
    const statusColors = {
      pending: 'bg-amber-500 text-white',
      preparing: 'bg-blue-500 text-white',
      ready: 'bg-emerald-500 text-white',
      delivered: 'bg-indigo-500 text-white',
      cancelled: 'bg-red-500 text-white',
      completed: 'bg-green-600 text-white'
    };
    return statusColors[status] || 'bg-gray-500 text-white';
  };

  if (loading) return (
    <div className="container mx-auto px-4 py-8 max-w-7xl">
      <button 
        onClick={() => navigate(-1)} 
        className="flex items-center text-blue-600 hover:text-blue-800 transition-colors mb-6"
      >
        <svg className="w-5 h-5 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 19l-7-7m0 0l7-7m-7 7h18" />
        </svg>
        Back
      </button>
      <div className="flex justify-center items-center min-h-[50vh]">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
      </div>
    </div>
  );

  if (error) return (
    <div className="container mx-auto px-4 py-8 max-w-7xl">
      <button 
        onClick={() => navigate(-1)} 
        className="flex items-center text-blue-600 hover:text-blue-800 transition-colors mb-6"
      >
        <svg className="w-5 h-5 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 19l-7-7m0 0l7-7m-7 7h18" />
        </svg>
        Back
      </button>
      <div className="bg-red-50 border-l-4 border-red-500 p-4 mb-6">
        <div className="flex">
          <div className="flex-shrink-0">
            <svg className="h-5 w-5 text-red-500" fill="currentColor" viewBox="0 0 20 20">
              <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
            </svg>
          </div>
          <div className="ml-3">
            <p className="text-sm text-red-700">{error}</p>
          </div>
        </div>
      </div>
    </div>
  );

  return (
    <div className="container mx-auto px-4 py-8 max-w-7xl">
      <button 
        onClick={() => navigate(-1)} 
        className="flex items-center text-blue-600 hover:text-blue-800 transition-colors mb-6"
      >
        <svg className="w-5 h-5 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 19l-7-7m0 0l7-7m-7 7h18" />
        </svg>
        Back
      </button>
      
      {/* User Profile Card */}
      {userProfile && (
        <div className="bg-gradient-to-r from-blue-600 to-indigo-600 rounded-xl shadow-lg mb-8 p-6 text-white">
          <div className="flex flex-col md:flex-row justify-between items-start md:items-center">
            <div className="mb-4 md:mb-0">
              <h2 className="text-2xl font-bold mb-2">
                Welcome back, {userProfile.name}!
              </h2>
              <p className="flex items-center text-blue-100">
                <svg className="w-4 h-4 mr-2" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
                  <path d="M2 3a1 1 0 011-1h2.153a1 1 0 01.986.836l.74 4.435a1 1 0 01-.54 1.06l-1.548.773a11.037 11.037 0 006.105 6.105l.774-1.548a1 1 0 011.059-.54l4.435.74a1 1 0 01.836.986V17a1 1 0 01-1 1h-2C7.82 18 2 12.18 2 5V3z" />
                </svg>
                {userProfile.phoneNumber}
              </p>
            </div>
            <div className="bg-white bg-opacity-20 rounded-lg px-4 py-3">
              <p className="text-lg font-semibold">
                Total Orders: <span className="text-2xl">{orders.length}</span>
              </p>
            </div>
          </div>
        </div>
      )}

      {/* Order Status Filter */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-800 mb-6">My Orders</h1>
        <div className="flex flex-wrap gap-2 mb-6">
          <button
            onClick={() => setFilterStatus('all')}
            className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors ${
              filterStatus === 'all'
                ? 'bg-blue-600 text-white shadow-md'
                : 'bg-white text-gray-700 border border-gray-200 hover:bg-gray-50'
            }`}
          >
            All Orders
          </button>
          {['pending', 'preparing', 'ready', 'delivered', 'cancelled', 'completed'].map(status => (
            <button
              key={status}
              onClick={() => setFilterStatus(status)}
              className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors ${
                filterStatus === status
                  ? `${getStatusColor(status)} shadow-md`
                  : 'bg-white text-gray-700 border border-gray-200 hover:bg-gray-50'
              }`}
            >
              {status.charAt(0).toUpperCase() + status.slice(1)}
            </button>
          ))}
        </div>
      </div>

      {/* Orders List */}
      {filteredOrders.length === 0 ? (
        <div className="text-center py-12 bg-white rounded-xl shadow-sm">
          <svg className="mx-auto h-12 w-12 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2" />
          </svg>
          <h3 className="mt-2 text-lg font-medium text-gray-900">
            {filterStatus === 'all' 
              ? 'No orders found' 
              : `No ${filterStatus} orders found`}
          </h3>
          <p className="mt-1 text-sm text-gray-500">
            {filterStatus === 'all' 
              ? 'You haven\'t placed any orders yet.' 
              : `Try changing your filter to see other orders.`}
          </p>
        </div>
      ) : (
        <div className="space-y-6">
          {filteredOrders.map((order) => (
            <div key={order._id} className="bg-white rounded-xl shadow-sm overflow-hidden border border-gray-100 hover:shadow-md transition-shadow">
              <div className="p-6">
                <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-4">
                  <div className="flex items-center mb-2 sm:mb-0">
                    <h3 className="text-lg font-semibold text-gray-900">
                      Order #{order._id.slice(-6).toUpperCase()}
                    </h3>
                    {order.paymentStatus === 'completed' && (
                      <span className="ml-3 inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                        Paid
                      </span>
                    )}
                  </div>
                  <span className={`px-3 py-1 rounded-full text-xs font-medium ${getStatusColor(order.status)}`}>
                    {order.status.charAt(0).toUpperCase() + order.status.slice(1)}
                  </span>
                </div>

                <div className="border-t border-gray-200 my-4"></div>

                <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mb-4">
                  <div className="flex items-center">
                    <svg className="w-5 h-5 text-blue-500 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2M5 11V9a2 2 0 012-2m0 0V5a2 2 0 012-2h6a2 2 0 012 2v2M7 7h10" />
                    </svg>
                    <span className="font-medium text-gray-700">Table {order.tableNumber}</span>
                  </div>
                  <div className="flex items-center sm:justify-end">
                    <svg className="w-5 h-5 text-gray-400 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                    </svg>
                    <span className="text-sm text-gray-500">
                      {new Date(order.createdAt).toLocaleString()}
                    </span>
                  </div>
                </div>

                <div className="bg-gray-50 rounded-lg p-4 mb-6">
                  <div className="flex items-center mb-3">
                    <svg className="w-5 h-5 text-blue-500 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z" />
                    </svg>
                    <h4 className="font-medium text-gray-800">Order Items</h4>
                  </div>
                  
                  <div className="divide-y divide-gray-200">
                    {order.items.map((item, index) => (
                      <div key={index} className="flex justify-between items-start py-3">
                        <div>
                          <p className="font-medium text-gray-900">{item.name}</p>
                          <p className="text-sm text-gray-500">
                            Quantity: {item.quantity}  ${item.price.toFixed(2)}
                          </p>
                          {item.specialInstructions && (
                            <p className="text-xs text-gray-400 mt-1">
                              Note: {item.specialInstructions}
                            </p>
                          )}
                        </div>
                        <span className="font-medium text-gray-900">
                          ${(item.quantity * item.price).toFixed(2)}
                        </span>
                      </div>
                    ))}
                  </div>
                </div>

                {order.comments && order.comments.length > 0 && (
                  <div className="mb-6">
                    <div className="flex items-center mb-3">
                      <svg className="w-5 h-5 text-blue-500 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                      </svg>
                      <h4 className="font-medium text-gray-800">Order Progress</h4>
                    </div>
                    <OrderTimeline comments={order.comments} />
                  </div>
                )}

                <div className="mt-4 flex flex-col sm:flex-row justify-between items-start sm:items-center">
                  <div className="mb-3 sm:mb-0">
                    <span className="text-xl font-semibold text-blue-600">
                      Total: ${order.totalAmount.toFixed(2)}
                    </span>
                    <div className="mt-2">
                      <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                        order.paymentStatus === 'completed' 
                          ? 'bg-green-100 text-green-800' 
                          : 'bg-yellow-100 text-yellow-800'
                      }`}>
                        Payment: {order.paymentStatus ? order.paymentStatus.charAt(0).toUpperCase() + order.paymentStatus.slice(1) : 'Pending'}
                      </span>
                      {order.paymentStatus === 'completed' && (
                        <span className="ml-3 text-sm text-gray-500">
                          via {order.paymentMethod}
                        </span>
                      )}
                    </div>
                  </div>
                  {order.status === 'delivered' && (!order.paymentStatus || order.paymentStatus === 'pending') && (
                    <button
                      onClick={() => handlePaymentClick(order)}
                      className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-blue-600 hover:bg-blue-700 transition-colors focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                    >
                      <svg className="-ml-1 mr-2 h-5 w-5" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 10h18M7 15h1m4 0h1m-7 4h12a3 3 0 003-3V8a3 3 0 00-3-3H6a3 3 0 00-3 3v8a3 3 0 003 3z" />
                      </svg>
                      Pay Now
                    </button>
                  )}
                </div>
              </div>
            </div>
          ))}
        </div>
      )}

      {/* Payment Modal */}
      {paymentOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50 transition-opacity">
          <div className="bg-white rounded-xl shadow-xl max-w-md w-full p-6 transform transition-all">
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-xl font-semibold text-gray-900">Complete Payment</h2>
              <button
                onClick={handlePaymentClose}
                className="text-gray-400 hover:text-gray-500"
                disabled={processingPayment}
              >
                <svg className="h-6 w-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>
            
            {paymentError && (
              <div className="bg-red-50 border-l-4 border-red-500 p-4 mb-4">
                <div className="flex">
                  <div className="flex-shrink-0">
                    <svg className="h-5 w-5 text-red-500" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                    </svg>
                  </div>
                  <div className="ml-3">
                    <p className="text-sm text-red-700">{paymentError}</p>
                  </div>
                </div>
              </div>
            )}

            <div className="bg-blue-50 rounded-lg p-4 mb-6">
              <p className="text-sm text-gray-500 mb-1">
                Order #{selectedOrder?._id.slice(-6).toUpperCase()}
              </p>
              <p className="text-2xl font-semibold text-gray-900">
                ${selectedOrder?.totalAmount.toFixed(2)}
              </p>
            </div>

            <div className="mb-6">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Payment Method <span className="text-red-500">*</span>
              </label>
              <div className="grid grid-cols-3 gap-3">
                {['cash', 'card', 'upi'].map((method) => (
                  <button
                    key={method}
                    type="button"
                    onClick={() => setPaymentMethod(method)}
                    disabled={processingPayment}
                    className={`rounded-lg border p-3 text-center transition-colors ${
                      processingPayment ? 'opacity-50 cursor-not-allowed' :
                      paymentMethod === method
                        ? 'border-blue-500 bg-blue-50'
                        : 'border-gray-200 hover:bg-gray-50'
                    }`}
                  >
                    <div className="flex flex-col items-center">
                      <div className={`w-6 h-6 rounded-full flex items-center justify-center mb-1 ${
                        paymentMethod === method ? 'bg-blue-500 text-white' : 'bg-gray-200'
                      }`}>
                        {paymentMethod === method && (
                          <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
                            <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                          </svg>
                        )}
                      </div>
                      <span className={`text-sm font-medium capitalize ${processingPayment ? 'text-gray-400' : ''}`}>
                        {method}
                      </span>
                    </div>
                  </button>
                ))}
              </div>
              {!paymentMethod && (
                <p className="mt-2 text-sm text-red-600">
                  Please select a payment method
                </p>
              )}
            </div>

            <div className="flex justify-end gap-3">
              <button
                onClick={handlePaymentClose}
                disabled={processingPayment}
                className={`px-4 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors ${
                  processingPayment ? 'opacity-50 cursor-not-allowed' : ''
                }`}
              >
                Cancel
              </button>
              <button
                onClick={handlePaymentSubmit}
                disabled={processingPayment || !paymentMethod}
                className={`flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg transition-colors focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 ${
                  (processingPayment || !paymentMethod) ? 
                  'opacity-50 cursor-not-allowed' : 
                  'hover:bg-blue-700'
                }`}
              >
                {processingPayment && (
                  <svg className="animate-spin -ml-1 mr-2 h-4 w-4 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                  </svg>
                )}
                {processingPayment ? 'Processing...' : 'Complete Payment'}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Orders;