import React, { useState, useEffect } from 'react';
import {
  Container,
  Grid,
  Card,
  CardContent,
  CardMedia,
  Typography,
  Button,
  Tabs,
  Tab,
  Box,
  Snackbar,
  useTheme,
  useMediaQuery,
  IconButton,
  Badge,
  Avatar,
  MenuItem,
  ListItemIcon,
  Divider
} from '@mui/material';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import {
  Dashboard,
  ExitToApp
} from '@mui/icons-material';
import axios from 'axios';
import { useNavigate, useLocation } from 'react-router-dom';
import BackButton from '../components/BackButton';
import { useAuth } from '../context/AuthContext';

const categories = ['All', 'Starters', 'Main Course', 'Desserts', 'Beverages'];

// Fallback images for each category
const fallbackImages = {
  'Starters': 'https://images.unsplash.com/photo-1623653387945-2fd25214f8fc?auto=format&fit=crop&w=500&q=60',
  'Main Course': 'https://images.unsplash.com/photo-1546069901-ba9599a7e63c?auto=format&fit=crop&w=500&q=60',
  'Desserts': 'https://images.unsplash.com/photo-1565958011703-44f9829ba187?auto=format&fit=crop&w=500&q=60',
  'Beverages': 'https://images.unsplash.com/photo-1544145945-f90425340c7e?auto=format&fit=crop&w=500&q=60',
  'default': 'https://images.unsplash.com/photo-1546069901-ba9599a7e63c?auto=format&fit=crop&w=500&q=60'
};

const Home = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const isTablet = useMediaQuery(theme.breakpoints.down('md'));

  const location = useLocation();
  const navigate = useNavigate();
  const { user, login, logout } = useAuth();

  const [selectedCategory, setSelectedCategory] = useState('All');
  const [foodItems, setFoodItems] = useState([]);
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('');
  const [cartCount, setCartCount] = useState(0);
  const [imageLoadStatus, setImageLoadStatus] = useState({});
  const [loading, setLoading] = useState(false);

  const showBackButton = location.pathname !== '/';

  //  Extract and store table number from URL query parameter
  useEffect(() => {
    const queryParams = new URLSearchParams(location.search);
    const table = queryParams.get("table");

    if (table) {
      sessionStorage.setItem("tableNumber", table);
      navigate("/", { replace: true }); // remove ?table=X from URL
    }
  }, [location.search, navigate]);

  // Fetch user profile when token exists but no user in context
  useEffect(() => {
    const fetchUserProfile = async () => {
      const token = localStorage.getItem('token');
      if (token && !user) {
        setLoading(true);
        try {
          const response = await axios.get('http://localhost:5000/api/auth/profile', {
            headers: {
              'Authorization': `Bearer ${token}`
            }
          });
          
          if (response.data) {
            await login({
              _id: response.data._id,
              name: response.data.name,
              phoneNumber: response.data.phoneNumber,
              role: response.data.role,
              orders: response.data.orders
            });
          }
        } catch (error) {
          console.error('Error fetching profile:', error);
          if (error.response?.status === 401) {
            localStorage.removeItem('token');
            localStorage.removeItem('user');
          }
        } finally {
          setLoading(false);
        }
      }
    };

    fetchUserProfile();
  }, [user, login]);

  // Fetch food items on mount
  useEffect(() => {
    const fetchFoodItems = async () => {
      try {
        const response = await axios.get('http://localhost:5000/api/menu');
        setFoodItems(response.data);
      } catch (error) {
        console.error('Error fetching food items:', error);
      }
    };

    fetchFoodItems();
  }, []);

  // Update cart count from localStorage
  useEffect(() => {
    const updateCartCount = () => {
      const savedCart = localStorage.getItem('cart');
      const cart = savedCart ? JSON.parse(savedCart) : [];
      const total = cart.reduce((sum, item) => sum + (item.quantity || 1), 0);
      setCartCount(total);
    };

    updateCartCount();
    window.addEventListener('storage', updateCartCount);
    return () => window.removeEventListener('storage', updateCartCount);
  }, []);

  const filteredItems = selectedCategory === 'All'
    ? foodItems
    : foodItems.filter(item => item.category === selectedCategory);

  const handleAddToCart = (item) => {
    const savedCart = sessionStorage.getItem('cart');
    const cart = savedCart ? JSON.parse(savedCart) : [];
    const existingItem = cart.find(cartItem => cartItem._id === item._id);
    if (existingItem) {
      existingItem.quantity += 1;
    } else {
      cart.push({ ...item, quantity: 1 });
    }
    sessionStorage.setItem('cart', JSON.stringify(cart));
    setSnackbarMessage(`${item.name} added to cart!`);
    setSnackbarOpen(true);

    const total = cart.reduce((sum, item) => sum + (item.quantity || 1), 0);
    setCartCount(total);
  };

  // Handle image loading
  const handleImageLoad = (itemId) => {
    setImageLoadStatus(prev => ({
      ...prev,
      [itemId]: 'loaded'
    }));
  };

  // Handle image error
  const handleImageError = (event, itemId, category) => {
    event.target.src = fallbackImages[category] || fallbackImages.default;
    setImageLoadStatus(prev => ({
      ...prev,
      [itemId]: 'error'
    }));
  };

  return (
    <Container 
      maxWidth="xl" 
      sx={{ 
        mt: { xs: 2, sm: 3, md: 4 },
        px: { xs: 1, sm: 2, md: 3 }
      }}
    >
      <Box 
        sx={{ 
          display: 'flex', 
          justifyContent: 'space-between', 
          alignItems: 'center',
          mb: { xs: 2, sm: 3, md: 4 }
        }}
      >
        <Box>{showBackButton && <BackButton />}</Box>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          {user && (
            <Box
              sx={{
                display: 'flex',
                alignItems: 'center',
                mr: 2,
                cursor: 'pointer',
                p: 1,
                borderRadius: 2,
                position: 'relative',
                '&:hover': {
                  bgcolor: 'action.hover',
                  '& .MuiTypography-root': {
                    color: 'primary.main'
                  },
                  '& .profile-dropdown': {
                    opacity: 1,
                    visibility: 'visible',
                    transform: 'translateY(0)'
                  }
                }
              }}
            >
              <Box
                className="profile-dropdown"
                sx={{
                  position: 'absolute',
                  top: '100%',
                  right: 0,
                  mt: 0.5,
                  width: 200,
                  bgcolor: 'background.paper',
                  borderRadius: 2,
                  boxShadow: 3,
                  opacity: 0,
                  visibility: 'hidden',
                  transform: 'translateY(-10px)',
                  transition: 'all 0.2s ease-in-out',
                  zIndex: 1000,
                  overflow: 'hidden'
                }}
              >
                <MenuItem onClick={() => navigate('/orders')}>
                  <ListItemIcon>
                    <Dashboard fontSize="small" />
                  </ListItemIcon>
                  Dashboard
                </MenuItem>
                <Divider />
                <MenuItem onClick={() => {
                  logout();
                  navigate('/');
                }}>
                  <ListItemIcon>
                    <ExitToApp fontSize="small" />
                  </ListItemIcon>
                  Logout
                </MenuItem>
              </Box>
              <Avatar 
                sx={{ 
                  width: 35, 
                  height: 35, 
                  bgcolor: 'secondary.main',
                  fontSize: '1rem',
                  mr: 1
                }}
              >
                {user.name ? user.name.charAt(0).toUpperCase() : 'U'}
              </Avatar>
              <Box>
                <Typography 
                  variant="body2" 
                  sx={{ 
                    fontWeight: 500,
                    display: { xs: 'none', sm: 'block' }
                  }}
                >
                  {user.name}
                </Typography>
                <Typography 
                  variant="caption" 
                  color="text.secondary"
                  sx={{ 
                    display: { xs: 'none', sm: 'block' }
                  }}
                >
                  {user.phoneNumber}
                </Typography>
              </Box>
            </Box>
          )}
          <IconButton color="primary" onClick={() => navigate('/cart')} sx={{ ml: 1 }}>
            <Badge badgeContent={cartCount} color="secondary" showZero>
              <ShoppingCartIcon fontSize="large" />
            </Badge>
          </IconButton>
        </Box>
      </Box>

      <Box 
        sx={{ 
          mb: { xs: 2, sm: 3, md: 4 },
          overflowX: 'auto',
          width: '100%'
        }}
      >
        <Tabs
          value={selectedCategory}
          onChange={(e, newValue) => setSelectedCategory(newValue)}
          centered={!isMobile}
          variant={isMobile ? "scrollable" : "standard"}
          scrollButtons={isMobile ? "auto" : false}
          allowScrollButtonsMobile
          sx={{
            '.MuiTab-root': {
              fontSize: { xs: '0.875rem', sm: '1rem' },
              minWidth: { xs: 'auto', sm: 120 },
              px: { xs: 1, sm: 2 },
            }
          }}
        >
          {categories.map((category) => (
            <Tab 
              key={category} 
              label={category} 
              value={category}
              sx={{
                whiteSpace: 'nowrap'
              }}
            />
          ))}
        </Tabs>
      </Box>

      <Grid 
        container 
        spacing={{ xs: 2, sm: 3, md: 4 }}
        sx={{ 
          mx: 'auto',
          maxWidth: '1600px'
        }}
      >
        {filteredItems.map((item) => (
          <Grid 
            item 
            key={item._id} 
            xs={12} 
            sm={6} 
            md={4} 
            lg={3}
            sx={{
              display: 'flex'
            }}
          >
            <Card 
              sx={{
                width: '100%',
                display: 'flex',
                flexDirection: 'column',
                transition: 'transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out',
                '&:hover': {
                  transform: 'translateY(-8px)',
                  boxShadow: (theme) => theme.shadows[8]
                }
              }}
            >
              <Box 
                sx={{ 
                  position: 'relative', 
                  paddingTop: '75%', // 4:3 Aspect Ratio
                  bgcolor: 'grey.100',
                  overflow: 'hidden'
                }}
              >
                <CardMedia
                  component="img"
                  alt={item.name}
                  src={item.image}
                  onLoad={() => handleImageLoad(item._id)}
                  onError={(e) => handleImageError(e, item._id, item.category)}
                  sx={{
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    width: '100%',
                    height: '100%',
                    objectFit: 'cover',
                    transition: 'opacity 0.3s ease-in-out',
                    opacity: imageLoadStatus[item._id] === 'loaded' ? 1 : 0.5,
                  }}
                />
                {!imageLoadStatus[item._id] && (
                  <Box
                    sx={{
                      position: 'absolute',
                      top: '50%',
                      left: '50%',
                      transform: 'translate(-50%, -50%)',
                      width: '30px',
                      height: '30px',
                      borderRadius: '50%',
                      borderTop: '2px solid',
                      borderColor: 'primary.main',
                      animation: 'spin 1s linear infinite',
                      '@keyframes spin': {
                        '0%': { transform: 'translate(-50%, -50%) rotate(0deg)' },
                        '100%': { transform: 'translate(-50%, -50%) rotate(360deg)' }
                      }
                    }}
                  />
                )}
              </Box>
              <CardContent 
                sx={{
                  flexGrow: 1,
                  display: 'flex',
                  flexDirection: 'column',
                  gap: { xs: 1, sm: 1.5 },
                  p: { xs: 2, sm: 3 }
                }}
              >
                <Typography 
                  variant="h5" 
                  component="div"
                  sx={{
                    fontSize: { xs: '1.125rem', sm: '1.25rem', md: '1.5rem' },
                    fontWeight: 600,
                    lineHeight: 1.2,
                    mb: 0.5
                  }}
                >
                  {item.name}
                </Typography>
                
                <Typography 
                  variant="body2" 
                  color="text.secondary"
                  sx={{
                    display: { xs: 'none', sm: 'block' },
                    fontSize: { sm: '0.875rem', md: '1rem' },
                    mb: 1,
                    flexGrow: 1
                  }}
                >
                  {item.description}
                </Typography>
                
                <Box sx={{ 
                  display: 'flex', 
                  justifyContent: 'space-between',
                  alignItems: 'center',
                  mt: 'auto',
                  gap: 2
                }}>
                  <Typography 
                    variant="h6" 
                    color="primary"
                    sx={{
                      fontSize: { xs: '1.125rem', sm: '1.25rem' },
                      fontWeight: 600
                    }}
                  >
                    ${item.price.toFixed(2)}
                  </Typography>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={() => handleAddToCart(item)}
                    sx={{
                      px: { xs: 2, sm: 2.5 },
                      py: { xs: 0.75, sm: 1 },
                      fontSize: { xs: '0.875rem', sm: '0.875rem' },
                      whiteSpace: 'nowrap'
                    }}
                  >
                    Add to Cart
                  </Button>
                </Box>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>

      <Snackbar
        open={snackbarOpen}
        autoHideDuration={1200}
        onClose={() => setSnackbarOpen(false)}
        message={snackbarMessage}
        anchorOrigin={{ 
          vertical: 'bottom', 
          horizontal: isMobile ? 'center' : 'right' 
        }}
        sx={{
          bottom: { xs: 16, sm: 24 }
        }}
      />
    </Container>
  );
};

export default Home;
