import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  TextField,
  Button,
  Paper,
  Grid,
  Card,
  CardContent,
  Chip,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
  FormControl,
  InputLabel,
  OutlinedInput,
  InputAdornment,
  Alert,
  Menu,
  MenuItem
} from '@mui/material';
import { alpha } from '@mui/material/styles';
import {
  Person as PersonIcon,
  Phone as PhoneIcon,
  TableBar as TableBarIcon,
  Add as AddIcon,
  Visibility as VisibilityIcon,
  VisibilityOff as VisibilityOffIcon,
  Edit as EditIcon,
  Delete as DeleteIcon,
  MoreVert as MoreVertIcon
} from '@mui/icons-material';
import axios from 'axios';

const WaiterManagement = () => {
  const [waiters, setWaiters] = useState([]);
  const [newWaiter, setNewWaiter] = useState({
    name: '',
    phoneNumber: '',
    password: '',
    assignedTables: ''
  });
  const [showPassword, setShowPassword] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [editingWaiter, setEditingWaiter] = useState(null);
  const [editDialogOpen, setEditDialogOpen] = useState(false);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [selectedWaiter, setSelectedWaiter] = useState(null);
  const [anchorEl, setAnchorEl] = useState(null);

  // Check if phone number already exists
  const checkPhoneNumberExists = (phoneNumber) => {
    return waiters.some(waiter => waiter.phoneNumber === phoneNumber);
  };

  // Fetch existing waiters
  const fetchWaiters = async () => {
    try {
      const response = await axios.get('http://localhost:5000/api/waiters');
      setWaiters(response.data);
    } catch (err) {
      setError('Failed to fetch waiters');
      console.error('Error fetching waiters:', err);
    }
  };

  useEffect(() => {
    fetchWaiters();
  }, []);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewWaiter(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const validateInput = () => {
    if (!newWaiter.name || !newWaiter.phoneNumber || !newWaiter.password || !newWaiter.assignedTables) {
      setError('All fields are required');
      return false;
    }
    if (!/^\d{10}$/.test(newWaiter.phoneNumber)) {
      setError('Phone number must be 10 digits');
      return false;
    }
    if (checkPhoneNumberExists(newWaiter.phoneNumber)) {
      setError('A waiter with this phone number already exists');
      return false;
    }
    if (newWaiter.password.length < 6) {
      setError('Password must be at least 6 characters');
      return false;
    }

    // Validate table numbers
    const tableNumbers = newWaiter.assignedTables.split(',').map(table => {
      const num = parseInt(table.trim());
      return isNaN(num) ? null : num;
    });

    if (tableNumbers.some(num => num === null || num < 1)) {
      setError('Invalid table numbers. Please enter positive numbers separated by commas.');
      return false;
    }

    return true;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');

    if (!validateInput()) return;

    try {
      // Convert assignedTables string to array of numbers and filter out invalid values
      const tables = newWaiter.assignedTables
        .split(',')
        .map(table => parseInt(table.trim()))
        .filter(table => !isNaN(table) && table > 0);

      if (tables.length === 0) {
        setError('Please enter at least one valid table number');
        return;
      }

      const response = await axios.post('http://localhost:5000/api/waiters/create', {
        name: newWaiter.name.trim(),
        phoneNumber: newWaiter.phoneNumber.trim(),
        password: newWaiter.password,
        assignedTables: tables
      });

      if (response.data) {
        setSuccess('Waiter created successfully!');
        setNewWaiter({
          name: '',
          phoneNumber: '',
          password: '',
          assignedTables: ''
        });
        fetchWaiters();
      }
    } catch (err) {
      const errorMessage = err.response?.data?.message || err.response?.data?.error || 'Failed to create waiter';
      setError(errorMessage);
      console.error('Error creating waiter:', err.response?.data || err.message);
    }
  };

  const handleEditClick = (waiter) => {
    setEditingWaiter({
      ...waiter,
      password: '', // Don't show existing password
      assignedTables: waiter.assignedTables.join(', ')
    });
    setEditDialogOpen(true);
    setAnchorEl(null);
  };

  const handleEditInputChange = (e) => {
    const { name, value } = e.target;
    setEditingWaiter(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleEditSubmit = async () => {
    try {
      const tables = editingWaiter.assignedTables
        .split(',')
        .map(table => parseInt(table.trim()))
        .filter(table => !isNaN(table));

      const updateData = {
        name: editingWaiter.name,
        phoneNumber: editingWaiter.phoneNumber,
        assignedTables: tables
      };

      // Only include password if it was changed
      if (editingWaiter.password) {
        updateData.password = editingWaiter.password;
      }

      const response = await axios.put(
        `http://localhost:5000/api/waiters/${editingWaiter._id}`,
        updateData
      );

      setSuccess('Waiter updated successfully!');
      setEditDialogOpen(false);
      fetchWaiters();
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to update waiter');
    }
  };

  const handleDeleteClick = (waiter) => {
    setSelectedWaiter(waiter);
    setDeleteDialogOpen(true);
    setAnchorEl(null);
  };

  const handleDeleteConfirm = async () => {
    try {
      await axios.delete(`http://localhost:5000/api/waiters/${selectedWaiter._id}`);
      setSuccess('Waiter deleted successfully!');
      setDeleteDialogOpen(false);
      setSelectedWaiter(null);
      fetchWaiters();
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to delete waiter');
    }
  };

  const handleMenuClick = (event, waiter) => {
    setSelectedWaiter(waiter);
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  return (
    <Box sx={{ 
      p: { xs: 2, sm: 3, md: 4 },
      maxWidth: 1200,
      mx: 'auto'
    }}>
      <Paper 
        elevation={2}
        sx={{ 
          p: { xs: 2, sm: 3, md: 4 },
          mb: 4,
          borderRadius: 2,
          backgroundColor: '#fff',
          transition: 'transform 0.2s, box-shadow 0.2s',
          '&:hover': {
            transform: 'translateY(-2px)',
            boxShadow: (theme) => theme.shadows[4]
          }
        }}
      >
        <Typography 
          variant="h5" 
          gutterBottom 
          sx={{ 
            mb: 3,
            fontWeight: 600,
            color: 'primary.main'
          }}
        >
          Create New Waiter
        </Typography>
        
        {error && (
          <Alert 
            severity="error" 
            sx={{ 
              mb: 3,
              borderRadius: 1
            }}
          >
            {error}
          </Alert>
        )}
        {success && (
          <Alert 
            severity="success"
            sx={{ 
              mb: 3,
              borderRadius: 1
            }}
          >
            {success}
          </Alert>
        )}

        <form onSubmit={handleSubmit}>
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="Name"
                name="name"
                value={newWaiter.name}
                onChange={handleInputChange}
                variant="outlined"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PersonIcon sx={{ color: 'primary.main' }} />
                    </InputAdornment>
                  ),
                }}
                sx={{
                  '& .MuiOutlinedInput-root': {
                    borderRadius: 1.5,
                    transition: 'all 0.2s',
                    '&:hover': {
                      backgroundColor: (theme) => alpha(theme.palette.primary.main, 0.02)
                    },
                    '&.Mui-focused': {
                      backgroundColor: (theme) => alpha(theme.palette.primary.main, 0.05)
                    }
                  }
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="Phone Number"
                name="phoneNumber"
                value={newWaiter.phoneNumber}
                onChange={handleInputChange}
                variant="outlined"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <PhoneIcon sx={{ color: 'primary.main' }} />
                    </InputAdornment>
                  ),
                }}
                sx={{
                  '& .MuiOutlinedInput-root': {
                    borderRadius: 1.5,
                    transition: 'all 0.2s',
                    '&:hover': {
                      backgroundColor: (theme) => alpha(theme.palette.primary.main, 0.02)
                    },
                    '&.Mui-focused': {
                      backgroundColor: (theme) => alpha(theme.palette.primary.main, 0.05)
                    }
                  }
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <FormControl fullWidth variant="outlined">
                <InputLabel>Password</InputLabel>
                <OutlinedInput
                  type={showPassword ? 'text' : 'password'}
                  name="password"
                  value={newWaiter.password}
                  onChange={handleInputChange}
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton
                        onClick={() => setShowPassword(!showPassword)}
                        edge="end"
                        sx={{ color: 'primary.main' }}
                      >
                        {showPassword ? <VisibilityOffIcon /> : <VisibilityIcon />}
                      </IconButton>
                    </InputAdornment>
                  }
                  label="Password"
                  sx={{
                    borderRadius: 1.5,
                    transition: 'all 0.2s',
                    '&:hover': {
                      backgroundColor: (theme) => alpha(theme.palette.primary.main, 0.02)
                    },
                    '&.Mui-focused': {
                      backgroundColor: (theme) => alpha(theme.palette.primary.main, 0.05)
                    }
                  }}
                />
              </FormControl>
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                fullWidth
                label="Assigned Tables"
                name="assignedTables"
                value={newWaiter.assignedTables}
                onChange={handleInputChange}
                placeholder="1, 2, 3"
                variant="outlined"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <TableBarIcon sx={{ color: 'primary.main' }} />
                    </InputAdornment>
                  ),
                }}
                sx={{
                  '& .MuiOutlinedInput-root': {
                    borderRadius: 1.5,
                    transition: 'all 0.2s',
                    '&:hover': {
                      backgroundColor: (theme) => alpha(theme.palette.primary.main, 0.02)
                    },
                    '&.Mui-focused': {
                      backgroundColor: (theme) => alpha(theme.palette.primary.main, 0.05)
                    }
                  }
                }}
              />
            </Grid>
            <Grid item xs={12}>
              <Button
                type="submit"
                variant="contained"
                color="primary"
                startIcon={<AddIcon />}
                size="large"
                sx={{
                  mt: 2,
                  py: 1.5,
                  px: 4,
                  borderRadius: 2,
                  textTransform: 'none',
                  fontWeight: 600,
                  boxShadow: 2,
                  '&:hover': {
                    transform: 'translateY(-1px)',
                    boxShadow: 4
                  },
                  transition: 'all 0.2s'
                }}
              >
                Create Waiter
              </Button>
            </Grid>
          </Grid>
        </form>
      </Paper>

      <Typography 
        variant="h5" 
        gutterBottom 
        sx={{ 
          mb: 3,
          fontWeight: 600,
          color: 'primary.main'
        }}
      >
        Existing Waiters
      </Typography>
      
      <Grid container spacing={3}>
        {waiters.map((waiter) => (
          <Grid item xs={12} sm={6} md={4} key={waiter._id}>
            <Card 
              sx={{
                height: '100%',
                borderRadius: 2,
                transition: 'all 0.2s',
                '&:hover': {
                  transform: 'translateY(-4px)',
                  boxShadow: 4
                }
              }}
            >
              <CardContent sx={{ p: 3 }}>
                <Box sx={{ 
                  display: 'flex', 
                  justifyContent: 'space-between', 
                  alignItems: 'flex-start', 
                  mb: 2 
                }}>
                  <Typography 
                    variant="h6" 
                    sx={{ 
                      fontWeight: 600,
                      color: 'text.primary'
                    }}
                  >
                    {waiter.name}
                  </Typography>
                  <IconButton 
                    onClick={(e) => handleMenuClick(e, waiter)} 
                    size="small"
                    sx={{
                      '&:hover': {
                        backgroundColor: (theme) => alpha(theme.palette.primary.main, 0.1)
                      }
                    }}
                  >
                    <MoreVertIcon />
                  </IconButton>
                </Box>
                <Typography 
                  sx={{ 
                    color: 'text.secondary',
                    display: 'flex',
                    alignItems: 'center',
                    gap: 1,
                    mb: 2
                  }}
                >
                  <PhoneIcon fontSize="small" />
                  {waiter.phoneNumber}
                </Typography>
                <Box sx={{ mt: 2 }}>
                  <Typography 
                    variant="subtitle2" 
                    gutterBottom
                    sx={{ 
                      color: 'text.secondary',
                      fontWeight: 600,
                      mb: 1
                    }}
                  >
                    Assigned Tables:
                  </Typography>
                  <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
                    {waiter.assignedTables.map((table) => (
                      <Chip
                        key={table}
                        label={`Table ${table}`}
                        size="small"
                        icon={<TableBarIcon />}
                        sx={{
                          borderRadius: 1,
                          backgroundColor: (theme) => alpha(theme.palette.primary.main, 0.1),
                          color: 'primary.main',
                          '& .MuiChip-icon': {
                            color: 'primary.main'
                          }
                        }}
                      />
                    ))}
                  </Box>
                </Box>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>

      {/* Menu for Edit/Delete options */}
      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleMenuClose}
        sx={{
          '& .MuiPaper-root': {
            borderRadius: 2,
            minWidth: 180,
            boxShadow: (theme) => theme.shadows[3]
          }
        }}
      >
        <MenuItem 
          onClick={() => handleEditClick(selectedWaiter)}
          sx={{
            py: 1.5,
            px: 2,
            '&:hover': {
              backgroundColor: (theme) => alpha(theme.palette.primary.main, 0.1)
            }
          }}
        >
          <EditIcon sx={{ mr: 2, color: 'primary.main' }} fontSize="small" />
          Edit
        </MenuItem>
        <MenuItem 
          onClick={() => handleDeleteClick(selectedWaiter)} 
          sx={{ 
            py: 1.5,
            px: 2,
            color: 'error.main',
            '&:hover': {
              backgroundColor: (theme) => alpha(theme.palette.error.main, 0.1)
            }
          }}
        >
          <DeleteIcon sx={{ mr: 2 }} fontSize="small" />
          Delete
        </MenuItem>
      </Menu>

      {/* Edit Dialog */}
      <Dialog 
        open={editDialogOpen} 
        onClose={() => setEditDialogOpen(false)} 
        maxWidth="sm" 
        fullWidth
        PaperProps={{
          sx: {
            borderRadius: 2,
            boxShadow: 24
          }
        }}
      >
        <DialogTitle sx={{ 
          pb: 1,
          pt: 3,
          px: 3,
          typography: 'h6',
          fontWeight: 600,
          color: 'primary.main'
        }}>
          Edit Waiter
        </DialogTitle>
        <DialogContent sx={{ p: 3 }}>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
            <TextField
              fullWidth
              label="Name"
              name="name"
              value={editingWaiter?.name || ''}
              onChange={handleEditInputChange}
              sx={{
                '& .MuiOutlinedInput-root': {
                  borderRadius: 1.5
                }
              }}
            />
            <TextField
              fullWidth
              label="Phone Number"
              name="phoneNumber"
              value={editingWaiter?.phoneNumber || ''}
              onChange={handleEditInputChange}
              sx={{
                '& .MuiOutlinedInput-root': {
                  borderRadius: 1.5
                }
              }}
            />
            <TextField
              fullWidth
              label="New Password (leave blank to keep current)"
              name="password"
              type="password"
              value={editingWaiter?.password || ''}
              onChange={handleEditInputChange}
              sx={{
                '& .MuiOutlinedInput-root': {
                  borderRadius: 1.5
                }
              }}
            />
            <TextField
              fullWidth
              label="Assigned Tables (comma-separated)"
              name="assignedTables"
              value={editingWaiter?.assignedTables || ''}
              onChange={handleEditInputChange}
              sx={{
                '& .MuiOutlinedInput-root': {
                  borderRadius: 1.5
                }
              }}
            />
          </Box>
        </DialogContent>
        <DialogActions sx={{ px: 3, py: 2, gap: 1 }}>
          <Button 
            onClick={() => setEditDialogOpen(false)}
            variant="outlined"
            sx={{
              borderRadius: 1.5,
              textTransform: 'none',
              px: 3
            }}
          >
            Cancel
          </Button>
          <Button 
            onClick={handleEditSubmit} 
            variant="contained" 
            color="primary"
            sx={{
              borderRadius: 1.5,
              textTransform: 'none',
              px: 3,
              '&:hover': {
                boxShadow: 2
              }
            }}
          >
            Save Changes
          </Button>
        </DialogActions>
      </Dialog>

      {/* Delete Confirmation Dialog */}
      <Dialog 
        open={deleteDialogOpen} 
        onClose={() => setDeleteDialogOpen(false)}
        PaperProps={{
          sx: {
            borderRadius: 2,
            width: '100%',
            maxWidth: 400
          }
        }}
      >
        <DialogTitle sx={{ 
          pb: 1,
          pt: 3,
          typography: 'h6',
          fontWeight: 600,
          color: 'error.main'
        }}>
          Confirm Delete
        </DialogTitle>
        <DialogContent sx={{ p: 3 }}>
          <Typography>
            Are you sure you want to delete waiter {selectedWaiter?.name}?
            This action cannot be undone.
          </Typography>
        </DialogContent>
        <DialogActions sx={{ px: 3, py: 2, gap: 1 }}>
          <Button 
            onClick={() => setDeleteDialogOpen(false)}
            variant="outlined"
            sx={{
              borderRadius: 1.5,
              textTransform: 'none',
              px: 3
            }}
          >
            Cancel
          </Button>
          <Button 
            onClick={handleDeleteConfirm} 
            variant="contained" 
            color="error"
            sx={{
              borderRadius: 1.5,
              textTransform: 'none',
              px: 3,
              '&:hover': {
                boxShadow: 2
              }
            }}
          >
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default WaiterManagement;
