import React, { useState } from 'react';
import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  Box,
  Avatar,
  Menu,
  MenuItem,
  Divider,
  useTheme,
  useMediaQuery,
  Button,
  ListItemIcon,
  ListItemText
} from '@mui/material';
import {
  Dashboard,
  ExitToApp,
  Menu as MenuIcon,
  PersonAdd,
  Person
} from '@mui/icons-material';
import { useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

const Navbar = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const location = useLocation();
  
  const navigate = useNavigate();
  const { user, logout } = useAuth();
  const [anchorEl, setAnchorEl] = useState(null);
  const [mobileMenuAnchorEl, setMobileMenuAnchorEl] = useState(null);
  const [profileMenuAnchorEl, setProfileMenuAnchorEl] = useState(null);

  const isAdminRoute = location.pathname === '/admin';
  const isChefRoute = location.pathname === '/chef';
  const isHomeRoute = location.pathname === '/';
  const isServerRoute = location.pathname === '/server';

  const handleProfileClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMobileMenuClick = (event) => {
    setMobileMenuAnchorEl(event.currentTarget);
  };

  const handleProfileIconClick = (event) => {
    setProfileMenuAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleMobileMenuClose = () => {
    setMobileMenuAnchorEl(null);
  };

  const handleProfileMenuClose = () => {
    setProfileMenuAnchorEl(null);
  };

  const handleLogout = async () => {
    handleClose();
    handleMobileMenuClose();
    handleProfileMenuClose();
    await logout();
    navigate('/');
  };

  const handleDashboard = () => {
    handleClose();
    handleMobileMenuClose();
    handleProfileMenuClose();
    navigate('/orders');
  };

  // Render role indicator for Admin, Chef, and Server routes
  const renderRoleIndicator = () => {
    if (isAdminRoute) {
      return (
        <Typography
          variant="h6"
          sx={{
            color: 'white',
            fontWeight: 'bold',
            fontSize: { xs: '1rem', sm: '1.25rem' }
          }}
        >
          Admin
        </Typography>
      );
    }
    if (isChefRoute) {
      return (
        <Typography
          variant="h6"
          sx={{
            color: 'white',
            fontWeight: 'bold',
            fontSize: { xs: '1rem', sm: '1.25rem' }
          }}
        >
          Chef
        </Typography>
      );
    }
    if (isServerRoute) {
      return (
        <Typography
          variant="h6"
          sx={{
            color: 'white',
            fontWeight: 'bold',
            fontSize: { xs: '1rem', sm: '1.25rem' }
          }}
        >
          Server
        </Typography>
      );
    }
    return null;
  };

  // Render profile section only for non-admin, non-chef, and non-server routes
  const renderProfileSection = () => {
    if (isAdminRoute || isChefRoute || isServerRoute) {
      return renderRoleIndicator();
    }

    if (isMobile) {
      return (
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          {user && !isServerRoute && (
            <IconButton
              color="inherit"
              edge="end"
              onClick={handleMobileMenuClick}
            >
              <MenuIcon />
            </IconButton>
          )}
        </Box>
      );
    }

    return (
      <Box sx={{ 
        display: 'flex', 
        alignItems: 'center', 
        gap: { sm: 2, md: 3 } 
      }}>
        {user && !isServerRoute && (
          <>
            <Box 
              sx={{ 
                display: 'flex', 
                alignItems: 'center', 
                cursor: 'pointer',
                '&:hover': {
                  opacity: 0.9
                }
              }}
              onClick={handleProfileClick}
            >
              <Avatar 
                sx={{ 
                  width: 32, 
                  height: 32, 
                  bgcolor: 'secondary.main',
                  fontSize: '1rem'
                }}
              >
                {user.name.charAt(0).toUpperCase()}
              </Avatar>
              <Typography 
                variant="body1" 
                sx={{ 
                  ml: 1, 
                  color: 'white',
                  display: { sm: 'none', md: 'block' }
                }}
              >
                {user.name}
              </Typography>
            </Box>
            <Menu
              anchorEl={anchorEl}
              open={Boolean(anchorEl)}
              onClose={handleClose}
              PaperProps={{
                sx: { width: '200px', mt: 1 }
              }}
            >
              <MenuItem 
                onClick={handleDashboard}
                sx={{ py: 1.5 }}
              >
                <ListItemIcon>
                  <Dashboard fontSize="small" />
                </ListItemIcon>
                <ListItemText>Orders</ListItemText>
              </MenuItem>
              <Divider />
              <MenuItem 
                onClick={handleLogout}
                sx={{ py: 1.5 }}
              >
                <ListItemIcon>
                  <ExitToApp fontSize="small" />
                </ListItemIcon>
                <ListItemText>Logout</ListItemText>
              </MenuItem>
            </Menu>
          </>
        )}
      </Box>
    );
  };

  // Render profile icon for homepage when user is logged in
  const renderHomeProfileIcon = () => {
    if (isHomeRoute && user) {
      return (
        <>
          <IconButton
            color="inherit"
            onClick={handleProfileIconClick}
            sx={{ ml: 2 }}
          >
            <Person />
          </IconButton>
          <Menu
            anchorEl={profileMenuAnchorEl}
            open={Boolean(profileMenuAnchorEl)}
            onClose={handleProfileMenuClose}
            PaperProps={{
              sx: { width: '200px', mt: 1 }
            }}
          >
            <MenuItem 
              onClick={handleDashboard}
              sx={{ py: 1.5 }}
            >
              <ListItemIcon>
                <Dashboard fontSize="small" />
              </ListItemIcon>
              <ListItemText>Orders</ListItemText>
            </MenuItem>
            <Divider />
            <MenuItem 
              onClick={handleLogout}
              sx={{ py: 1.5 }}
            >
              <ListItemIcon>
                <ExitToApp fontSize="small" />
              </ListItemIcon>
              <ListItemText>Logout</ListItemText>
            </MenuItem>
          </Menu>
        </>
      );
    }
    return null;
  };

  return (
    <AppBar 
      position="static" 
      sx={{
        boxShadow: 2
      }}
    >
      <Toolbar
        sx={{
          justifyContent: 'space-between',
          minHeight: { xs: '56px', sm: '64px' },
          px: { xs: 1, sm: 2, md: 3 }
        }}
      >
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <Typography
            variant="h6"
            component="div"
            sx={{
              fontSize: { xs: '1.25rem', sm: '1.5rem' },
              fontWeight: 'bold',
              cursor: 'pointer',
              flexGrow: 0
            }}
            onClick={() => navigate('/')}
          >
            TestCafe
          </Typography>
          {renderHomeProfileIcon()}
        </Box>

        {renderProfileSection()}

        {isMobile && user && !isAdminRoute && !isChefRoute && !isServerRoute && (
          <Menu
            anchorEl={mobileMenuAnchorEl}
            open={Boolean(mobileMenuAnchorEl)}
            onClose={handleMobileMenuClose}
            PaperProps={{
              sx: { width: '200px', mt: 1 }
            }}
          >
            <MenuItem 
              onClick={handleDashboard}
              sx={{ py: 1.5 }}
            >
              <ListItemIcon>
                <Dashboard fontSize="small" />
              </ListItemIcon>
              <ListItemText>Orders</ListItemText>
            </MenuItem>
            <Divider />
            <MenuItem 
              onClick={handleLogout}
              sx={{ py: 1.5 }}
            >
              <ListItemIcon>
                <ExitToApp fontSize="small" />
              </ListItemIcon>
              <ListItemText>Logout</ListItemText>
            </MenuItem>
          </Menu>
        )}
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;