import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Container,
  Typography,
  Paper,
  Grid,
  Card,
  CardContent,
  useTheme,
  IconButton
} from '@mui/material';
import {
  Payments as PaymentsIcon,
  AccountBalanceWallet as WalletIcon,
  CalendarToday as CalendarIcon,
  CreditCard as UpiIcon,
  Money as CashIcon,
  ArrowBack as ArrowBackIcon
} from '@mui/icons-material';
import BackButton from '../components/BackButton';

const StatCard = ({ title, value, icon, color }) => {
  const theme = useTheme();
  return (
    <Card 
      sx={{ 
        height: '100%',
        transition: 'transform 0.2s, box-shadow 0.2s',
        '&:hover': {
          transform: 'translateY(-4px)',
          boxShadow: theme.shadows[4]
        }
      }}
    >
      <CardContent sx={{ p: 3 }}>
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 2.5 }}>
          <Box sx={{ 
            display: 'flex', 
            alignItems: 'center', 
            justifyContent: 'center',
            bgcolor: `${color || theme.palette.primary.main}15`,
            borderRadius: '12px',
            p: 1.5,
            mr: 2
          }}>
            {React.cloneElement(icon, { 
              sx: { fontSize: '2rem', color: color || theme.palette.primary.main } 
            })}
          </Box>
          <Typography variant="h6" sx={{ fontWeight: 500 }}>
            {title}
          </Typography>
        </Box>
        <Typography variant="h4" sx={{ 
          color: color || theme.palette.primary.main,
          fontWeight: 600,
          mt: 1
        }}>
          {value}
        </Typography>
      </CardContent>
    </Card>
  );
};

const Accountance = () => {
  const navigate = useNavigate();
  const theme = useTheme();  const [stats, setStats] = useState({
    totalOrders: 0,
    pendingOrders: 0,
    completedOrders: 0,
    totalAmount: 0
  });

  const handleBack = () => {
    navigate('/admin');
  };

  useEffect(() => {
    fetchAccountanceData();
  }, []);  const fetchAccountanceData = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/orders/accountance');
      
      if (!response.ok) {
        throw new Error(`Failed to fetch accountance data: ${response.status}`);
      }
      
      const data = await response.json();
      console.log('Received accountance data:', data);
      
      // Ensure all values are numbers
      setStats({
        totalOrders: Number(data.totalOrders) || 0,
        pendingOrders: Number(data.pendingOrders) || 0,
        completedOrders: Number(data.completedOrders) || 0,
        totalAmount: Number(data.totalAmount) || 0
      });
    } catch (error) {
      console.error('Error fetching accountance data:', error);
    }
  };
  const formatCurrency = (amount) => {
    if (typeof amount !== 'number') return '0.00';
    return `${amount.toFixed(2)}`;
  };

  return (
    <Box sx={{ bgcolor: '#f5f5f5', minHeight: '100vh', py: 4, position: 'relative' }}>
      {/* Floating back button */}
      <IconButton
        onClick={handleBack}
        sx={{
          position: 'fixed',
          bottom: 24,
          right: 24,
          bgcolor: theme.palette.primary.main,
          width: 56,
          height: 56,
          boxShadow: '0 8px 16px rgba(0,0,0,0.1)',
          zIndex: 1000,
          transition: 'all 0.3s ease-in-out',
          '&:hover': {
            bgcolor: theme.palette.primary.dark,
            transform: 'scale(1.1) rotate(-10deg)',
            boxShadow: '0 12px 20px rgba(0,0,0,0.15)',
          },
          '@keyframes float': {
            '0%': { transform: 'translateY(0px)' },
            '50%': { transform: 'translateY(-10px)' },
            '100%': { transform: 'translateY(0px)' }
          },
          animation: 'float 3s ease-in-out infinite'
        }}
      >
        <ArrowBackIcon sx={{ 
          fontSize: 30, 
          color: 'white',
          transition: 'transform 0.3s ease-in-out',
          '&:hover': {
            transform: 'scale(1.1)'
          }
        }} />
      </IconButton>

      <Container maxWidth="xl">
        <Box sx={{ 
          display: 'flex', 
          alignItems: 'center', 
          mb: 4,
        }}>
          <Typography
            variant="h4" 
            sx={{ 
              fontWeight: 600,
              color: theme.palette.grey[900]
            }}
          >
            Restaurant Accountance
          </Typography>
        </Box>

        <Grid container spacing={3}>
          {/* Orders Overview */}
          <Grid item xs={12}>
            <Paper 
              elevation={0}
              sx={{ 
                p: 3, 
                mb: 3,
                borderRadius: 2,
                bgcolor: 'white'
              }}
            >
              <Typography 
                variant="h5" 
                gutterBottom 
                sx={{ 
                  mb: 3,
                  fontWeight: 500,
                  color: theme.palette.grey[800]
                }}
              >
                Orders Overview
              </Typography>              <Grid container spacing={3}>
                <Grid item xs={12} sm={6} md={3}>
                  <StatCard
                    title="Total Orders"
                    value={stats.totalOrders}
                    icon={<PaymentsIcon />}
                    color={theme.palette.primary.main}
                  />
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                  <StatCard
                    title="Pending Orders"
                    value={stats.pendingOrders}
                    icon={<CalendarIcon />}
                    color={theme.palette.warning.main}
                  />
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                  <StatCard
                    title="Completed Orders"
                    value={stats.completedOrders}
                    icon={<PaymentsIcon />}
                    color={theme.palette.success.main}
                  />
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                  <StatCard
                    title="Total Revenue"
                    value={formatCurrency(stats.totalAmount)}
                    icon={<WalletIcon />}
                    color="#1976d2"
                  />
                </Grid>
                <Grid item xs={12} sm={6} md={4}>
                  <StatCard
                    title="Cash Orders"
                    value={stats.cashOrders}
                    icon={<CashIcon />}
                    color={theme.palette.warning.main}
                  />
                </Grid>
                <Grid item xs={12} sm={6} md={4}>
                  <StatCard
                    title="UPI Orders"
                    value={stats.upiOrders}
                    icon={<UpiIcon />}
                    color={theme.palette.info.main}
                  />
                </Grid>
                <Grid item xs={12} sm={6} md={4}>
                  <StatCard
                    title="Today's Revenue"
                    value={formatCurrency(stats.dailyRevenue)}
                    icon={<CalendarIcon />}
                    color={theme.palette.secondary.main}
                  />
                </Grid>
              </Grid>
            </Paper>          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default Accountance;
