import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Container,
  Typography,
  Card,
  CardContent,
  Grid,
  Chip,
  Stack,
  useTheme,
  useMediaQuery,
  Button,
  Tabs,
  Tab
} from '@mui/material';
import {
  TableRestaurant as TableRestaurantIcon,
  AccessTime as AccessTimeIcon,
  AccountBalanceWallet as AccountBalanceWalletIcon,
  Person as PersonIcon
} from '@mui/icons-material';
import WaiterManagement from '../components/WaiterManagement';

const OrderCard = ({ order }) => {
  const theme = useTheme();
  
  if (!order) return null;

  const getStatusColor = (status) => {
    const colors = {
      pending: theme.palette.warning.main,
      accepted: theme.palette.info.main,
      preparing: theme.palette.info.dark,
      ready: theme.palette.success.light,
      delivered: theme.palette.success.dark,
      cancelled: theme.palette.error.main
    };
    return colors[status?.toLowerCase()] || theme.palette.grey[500];
  };

  const formatTime = (dateString) => {
    if (!dateString) return '';
    return new Date(dateString).toLocaleTimeString('en-US', {
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const orderId = order._id ? order._id.toString().slice(-6) : 'N/A';
  const status = order.status || 'pending';
  const paymentStatus = order.paymentStatus || 'pending';
  const paymentMethod = order.paymentMethod || 'N/A';
  const totalAmount = order.totalAmount || 0;
  const tableNumber = order.tableNumber || 'N/A';
  const statusColor = getStatusColor(status);

  return (
    <Card 
      elevation={0}
      sx={{
        height: '100%',
        borderRadius: 3,
        background: theme.palette.background.paper,
        transition: 'all 0.3s ease-in-out',
        border: `1px solid ${theme.palette.divider}`,
        '&:hover': {
          transform: 'translateY(-4px)',
          boxShadow: `0 4px 20px ${theme.palette.mode === 'dark' ? 'rgba(0,0,0,0.4)' : 'rgba(0,0,0,0.1)'}`,
          borderColor: 'transparent'
        }
      }}
    >
      <CardContent sx={{ p: 3 }}>
        <Stack spacing={2.5}>
          <Box sx={{ 
            display: 'flex', 
            justifyContent: 'space-between', 
            alignItems: 'center'
          }}>
            <Box sx={{ 
              display: 'flex', 
              alignItems: 'center', 
              gap: 1,
              px: 2,
              py: 0.5,
              borderRadius: 2,
              bgcolor: theme.palette.mode === 'dark' ? 'grey.900' : 'grey.100'
            }}>
              <Typography 
                variant="body2" 
                sx={{ 
                  fontWeight: 600,
                  color: theme.palette.mode === 'dark' ? 'grey.400' : 'grey.700'
                }}
              >
                #{orderId}
              </Typography>
            </Box>
            <Box sx={{ 
              display: 'flex', 
              alignItems: 'center', 
              gap: 1,
              color: theme.palette.text.secondary
            }}>
              <AccessTimeIcon sx={{ fontSize: 16 }} />
              <Typography variant="caption" sx={{ fontWeight: 500 }}>
                {formatTime(order.createdAt)}
              </Typography>
            </Box>
          </Box>

          <Box sx={{ 
            display: 'flex', 
            alignItems: 'center', 
            gap: 2,
            p: 2,
            borderRadius: 2,
            bgcolor: `${theme.palette.primary.main}08`
          }}>
            <Box sx={{
              width: 40,
              height: 40,
              borderRadius: 2,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              bgcolor: theme.palette.primary.main
            }}>
              <TableRestaurantIcon sx={{ color: 'white', fontSize: 20 }} />
            </Box>
            <Box>
              <Typography variant="body2" color="text.secondary" gutterBottom>
                Table Number
              </Typography>
              <Typography variant="h6" sx={{ fontWeight: 600 }}>
                {tableNumber}
              </Typography>
            </Box>
          </Box>

          <Box sx={{ display: 'flex', gap: 2 }}>
            <Chip
              label={status.toUpperCase()}
              sx={{
                bgcolor: `${statusColor}15`,
                color: statusColor,
                fontWeight: 600,
                px: 1,
                '& .MuiChip-label': {
                  px: 1.5
                }
              }}
            />
            <Chip
              size="small"
              label={
                paymentStatus === 'completed' 
                  ? `Paid with ${paymentMethod.toUpperCase()}`
                  : 'PAYMENT PENDING'
              }
              color={paymentStatus === 'completed' ? 'success' : 'warning'}
              sx={{ 
                fontWeight: 500,
                '& .MuiChip-label': {
                  px: 1.5
                }
              }}
            />
          </Box>

          <Box sx={{ 
            p: 2,
            borderRadius: 2,
            bgcolor: theme.palette.mode === 'dark' ? 'grey.900' : 'grey.50'
          }}>
            <Stack spacing={1.5}>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Typography variant="body2" color="text.secondary">
                  Payment Status
                </Typography>
                <Typography variant="body2" sx={{ 
                  fontWeight: 600,
                  color: paymentStatus === 'completed' ? theme.palette.success.main : theme.palette.warning.main
                }}>
                  {paymentStatus === 'completed' 
                    ? `Paid with ${paymentMethod}` 
                    : 'Payment Pending'}
                </Typography>
              </Box>
              <Box sx={{ 
                display: 'flex', 
                justifyContent: 'space-between', 
                alignItems: 'center',
                pt: 1.5,
                borderTop: `1px solid ${theme.palette.divider}`
              }}>
                <Typography variant="body2" color="text.secondary">
                  Total Amount
                </Typography>
                <Typography 
                  variant="h6" 
                  sx={{ 
                    fontWeight: 700,
                    color: theme.palette.primary.main
                  }}
                >
                  {totalAmount.toFixed(2)}
                </Typography>
              </Box>
            </Stack>
          </Box>
        </Stack>
      </CardContent>
    </Card>
  );
};

const StatsCard = ({ icon: Icon, title, value, color }) => {
  const theme = useTheme();
  return (
    <Card
      elevation={0}
      sx={{
        borderRadius: 3,
        background: `linear-gradient(135deg, ${color}08, ${color}15)`,
        transition: 'transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out',
        '&:hover': {
          transform: 'translateY(-4px)',
          boxShadow: `0 4px 20px ${color}20`
        }
      }}
    >
      <CardContent sx={{ p: 3 }}>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
          <Box
            sx={{
              width: 48,
              height: 48,
              borderRadius: 2,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              bgcolor: `${color}15`,
              color: color
            }}
          >
            <Icon sx={{ fontSize: 24 }} />
          </Box>
          <Box>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              {title}
            </Typography>
            <Typography variant="h5" sx={{ fontWeight: 600, color: theme.palette.text.primary }}>
              {value}
            </Typography>
          </Box>
        </Box>
      </CardContent>
    </Card>
  );
};

const AdminDashboard = () => {
  const [activeTab, setActiveTab] = useState('orders');
  const theme = useTheme();
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    fetchOrders();
    const interval = setInterval(fetchOrders, 30000);
    return () => clearInterval(interval);
  }, []);

  const fetchOrders = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/orders/all-orders');
      if (!response.ok) {
        throw new Error('Failed to fetch orders');
      }
      const data = await response.json();
      setOrders(data);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  const handleAccountanceClick = () => {
    navigate('/accountance');
  };

  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  if (loading) return (
    <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
      <Typography>Loading...</Typography>
    </Box>
  );

  if (error) return (
    <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
      <Typography color="error">{error}</Typography>
    </Box>
  );

  return (
    <Box 
      sx={{ 
        bgcolor: theme.palette.mode === 'dark' ? 'grey.900' : 'grey.50',
        minHeight: '100vh',
        py: 4
      }}
    >
      <Container maxWidth="xl">
        <Box 
          sx={{ 
            mb: 6,
            display: 'flex',
            flexDirection: { xs: 'column', md: 'row' },
            justifyContent: 'space-between',
            alignItems: { xs: 'flex-start', md: 'center' },
            gap: 3
          }}
        >
          <Box>
            <Typography 
              variant="h4" 
              sx={{ 
                fontWeight: 700,
                background: `linear-gradient(135deg, ${theme.palette.primary.main}, ${theme.palette.primary.dark})`,
                WebkitBackgroundClip: 'text',
                WebkitTextFillColor: 'transparent',
                mb: 1
              }}
            >
              Admin Dashboard
            </Typography>
            <Typography variant="body1" color="text.secondary">
              Manage your restaurant operations efficiently
            </Typography>
          </Box>

          <Button
            variant="contained"
            onClick={handleAccountanceClick}
            startIcon={<AccountBalanceWalletIcon />}
            sx={{
              px: 3,
              py: 1.5,
              borderRadius: 2,
              textTransform: 'none',
              background: `linear-gradient(135deg, ${theme.palette.primary.main}, ${theme.palette.primary.dark})`,
              '&:hover': {
                background: `linear-gradient(135deg, ${theme.palette.primary.dark}, ${theme.palette.primary.main})`
              }
            }}
          >
            View Accountance
          </Button>
        </Box>

        {/* Stats Overview */}
        <Grid container spacing={3} sx={{ mb: 6 }}>
          <Grid item xs={12} sm={6} md={3}>
            <StatsCard
              icon={TableRestaurantIcon}
              title="Total Orders"
              value={orders.length}
              color={theme.palette.primary.main}
            />
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <StatsCard
              icon={AccessTimeIcon}
              title="Pending Orders"
              value={orders.filter(o => o.status === 'pending').length}
              color={theme.palette.warning.main}
            />
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <StatsCard
              icon={TableRestaurantIcon}
              title="Completed Orders"
              value={orders.filter(o => o.status === 'delivered').length}
              color={theme.palette.success.main}
            />
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <StatsCard
              icon={AccountBalanceWalletIcon}
              title="Total Revenue"
              value={`${orders.reduce((sum, order) => sum + (order.totalAmount || 0), 0).toFixed(2)}`}
              color={theme.palette.info.main}
            />
          </Grid>
        </Grid>

        {/* Navigation Tabs */}
        <Box 
          sx={{ 
            mb: 4,
            bgcolor: 'background.paper',
            borderRadius: 3,
            boxShadow: '0 2px 12px rgba(0,0,0,0.03)'
          }}
        >
          <Tabs
            value={activeTab}
            onChange={handleTabChange}
            variant="scrollable"
            scrollButtons="auto"
            sx={{
              '& .MuiTab-root': {
                minHeight: 64,
                textTransform: 'none',
                fontSize: '1rem',
                fontWeight: 500,
                color: 'text.secondary',
                '&.Mui-selected': {
                  color: 'primary.main',
                  fontWeight: 600
                }
              },
              '& .MuiTabs-indicator': {
                height: 3,
                borderRadius: '3px 3px 0 0',
                background: `linear-gradient(90deg, ${theme.palette.primary.main}, ${theme.palette.primary.light})`
              }
            }}
          >
            <Tab
              label="Orders"
              value="orders"
              icon={<TableRestaurantIcon />}
              iconPosition="start"
              sx={{ px: 4 }}
            />
            <Tab
              label="Waiter Management"
              value="waiters"
              icon={<PersonIcon />}
              iconPosition="start"
              sx={{ px: 4 }}
            />
          </Tabs>
        </Box>

        {/* Content Area */}
        {activeTab === 'orders' && (
          <Grid container spacing={3}>
            {orders.map((order) => (
              <Grid item xs={12} sm={6} md={4} lg={3} key={order._id}>
                <OrderCard order={order} />
              </Grid>
            ))}
          </Grid>
        )}

        {activeTab === 'waiters' && <WaiterManagement />}
      </Container>
    </Box>
  );
};

export default AdminDashboard;